.contactUs{
    width: 100%;
    padding: 6rem 10rem;
    min-height: 85vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.contactUs img.belowLens{
    position: absolute;
    bottom: 0;
    width: 500px;
    right: 0;
    transform: rotate(180deg);
    z-index: -10;
}

.contactUs img.whitestar{
    position: absolute;
    bottom: 10%;
    width: 20px;
    right: 5%;
    z-index: -1;
}

.contactUs img.greyStar{
    position: absolute;
    top: 5%;
    width: 15px;
    right: 20%;
    z-index: -1;
}

.contactUs img.purpleStar{
    position: absolute;
    bottom: 25%;
    width: 20px;
    left: 49%;
    z-index: -1;
}

.contactUs img.otherStar{
    position: absolute;
    top: 10%;
    width: 15px;
    left: 10%;
    z-index: -1;
}

.getInTouch{
    opacity: 0;
    animation: fadeInDown 3s ease-in-out forwards;
}

.getInTouch h1{
    font-family: 'Clash Display';
    color: #D434FE;
}

.getInTouch p{
    font-family: 'Montserrat';
    margin-top: 1rem;
    font-size: 16px;
}

.getInTouch .share{
    margin-top: 1.5rem;
}

.getInTouch .share h3{
    font-family: 'Montserrat';
    color: #D434FE;
    font-size: 16px;
}

.getInTouch .share ul.follow{
    margin-top: 1rem;
    display: flex;
    align-items: center;
    list-style-type: none;
    width: 100%;
    gap: 1rem;
}

.getInTouch .share ul.follow li a img:hover{
    transform: scale(1.15);
    transition: all .35s ease-in-out;
}

.box{
    padding: 3rem;
    width: 50%;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    opacity: 0;
    animation: fadeInLeft 3s ease-in-out 1.5s forwards;
}

.box h2{
    font-size: 20px;
    color: #D434FE;
    font-family: 'Clash Display';
}

.box p.emailMessage{
    font-size: 16px;
    font-family: 'Montserrat';
    margin-top: 1rem;
    display: none;
}

.box form{
    margin-top: 1.5rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.box form input{
    width: 100%;
    height: 45px;
    padding-left: 1rem;
    border-radius: 4px;
    border: 1px solid #FFF;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    color: #FFF;
    font-family: 'Montserrat';
    outline: none;
}

.box form input.displayInp{
    display: none;
}

.box form input.noDisplayInp{
    display: block;
}

.box form input::placeholder, .box form textarea::placeholder{
    color: #FFF;
}

.box form textarea{
    width: 100%;
    height: 110px;
    padding: 1rem;
    border-radius: 4px;
    border: 1px solid #FFF;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    outline: none;
    color: #FFF;
    font-family: 'Montserrat';
}

.box form button{
    width: 150px;
    height: 50px;
    margin: 0 auto;
    border: none;
    font-family: 'Montserrat';
    font-weight: 600;
    color: #FFF;
    border-radius: 4px;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
    cursor: pointer;
}

.box form button:hover{
    background: linear-gradient(90deg, #903AFF 0%, #FE34B9 56.42%,  #D434FE 99.99%,  #FF26B9 100%);
    transition: all .3s ease-in-out;
}

@media screen and (max-width: 1130px){
    .contactUs{
        padding: 6rem;
    }
    .box h2{
        font-size: 17px;
    }
}

@media screen and (max-width: 900px){
    .contactUs{
        padding: 2rem;
    }
}

@media screen and (max-width: 800px){
    .contactUs{
        flex-direction: column-reverse;
        padding: 1rem 2rem;
    }
    .box{
        width: 100%;
        border-radius: 0;
        background: transparent;
        box-shadow: none;
        opacity: 0;
        animation: fadeInLeft 3s ease-in-out 1.5s forwards;
    }
    .contactUs img.otherStar{
        top: 5%;
        left: 5%;
    }
    .contactUs img.purpleStar{
        bottom: 20%;
        left: 5%;
    }
    .getInTouch h1, .getInTouch p{
       display: none;
    }
    .getInTouch .share h3{
        text-align: center;
    }
    .box p.emailMessage{
        display: block;
    }
    .box form input.displayInp{
        display: block;
    }
    .box form input.noDisplayInp{
        display: none;
    }
}

@media screen and (max-width: 590px){
    .contactUs{
        padding: 0.5rem;
    }
    .box{
        padding: 2rem;
    }
    .box h2{
        font-size: 15px;
    }
    .box p.emailMessage{
        font-size: 13px;
    }
}
