@font-face {
  font-family: 'Clash Display';
  src: url('./assets/Fonts/ClashDisplay-Medium.woff') format('woff');
}

@font-face {
  font-family: 'Typo Hoop';
  src: url('./assets/Fonts/TypoHoop_Regular_Demo.otf') format('woff');
}

@font-face {
  font-family: 'Made Tommy Outline';
  src: url('./assets/Fonts/MADETOMMYBoldOutline_PERSONAL.otf') format('woff');
}

@import 'animate.css';

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body{
  width: 100%;
  min-height: 100vh;
  background-color: #150E28;
  color: #fff;
}

.firstPurple{
  position: absolute;
  left: 0;
  top: 10%;
  bottom: 20%;
  z-index: -1;
  width: 50%;
  opacity: 0;
  animation: fadeIn 2s ease-in-out 2s forwards;
}

@media screen and (max-width: 800px) {
  .firstPurple{
    top: 10%;
    left: -10%;
  }
}