.footer{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #100B20;
    padding: 3rem 10rem;
    position: relative;
}

.footer .whiteStar{
    position: absolute;
    width: 15px;
    top: 20%;
    left: 7%;
}

.footer .whiteStarTwo{
    position: absolute;
    width: 15px;
    bottom: 25%;
    right: 10%;
}

.footer .greyStar{
    position: absolute;
    width: 15px;
    top: 20%;
    right: 30%;
}

.footer .purpleStar{
    position: absolute;
    width: 15px;
    bottom: 25%;
    left: 50%;
}

.topFooter{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.topFooter .getLinked{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.topFooter .getLinked .top{
    width: 100%;
}

.topFooter .getLinked .top a{
    font-size: 30px;
    font-family: 'Clash Display';
    text-decoration: none;
    color: #fff;
}

.topFooter .getLinked .top a p span{
    color: #D434FE;
}

.topFooter .getLinked .top p.topText{
    font-family: 'Montserrat';
    margin-top: 1rem;
    font-size: 14px;
    width: 400px;
}

.topFooter .getLinked .down{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}

.topFooter .getLinked .down a{
    color: #fff;
    text-decoration: none;
    font-family: 'Montserrat';
    font-size: 14px;
    font-weight: 600;
}

.topFooter .getLinked .down .line{
    width: 3px;
    height: 25px;
    background-color: #D434FE;
}

.topFooter .usefulLinks h3{
    font-size: 16px;
    color: #D434FE;
    font-family: 'Montserrat';
}

.topFooter .usefulLinks ul.mainList{
    list-style-type: none;
    margin-top: 1rem;
}

.topFooter .usefulLinks ul.mainList li{
    margin-top: 0.5rem;
}

.topFooter .usefulLinks ul.mainList li a{
    text-decoration: none;
    color: #fff;
    font-family: 'Montserrat';
    font-size: 14px;
}

.topFooter .usefulLinks ul.mainList li .follow{
    display: flex;
    list-style-type: none;
    gap: 1em;
    align-items: center;
    font-family: 'Montserrat';
    font-size: 14px;
}

.topFooter .usefulLinks ul.mainList li .follow a img:hover{
    transform: scale(1.15);
    transition: transform .3s ease-in-out;
}

.topFooter .contact h3{
    color: #D434FE;
    font-family: 'Montserrat';
    font-size: 16px;
}

.topFooter .contact ul{
    margin-top: 1rem;
    list-style-type: none;
}

.topFooter .contact ul li{
    display: flex;
    gap: 0.5rem;
    align-items: flex-start;
    margin-top: 1rem;
    font-family: 'Montserrat';
    font-size: 14px;
}

.footer .rights{
    font-family: 'Montserrat';
    font-size: 14px;
    margin-top: 2rem;
}

@media screen and (max-width: 1135px){
    .footer{
        padding: 2rem 6rem;
    }
}

@media screen and (max-width: 990px){
    .topFooter .getLinked .top p.topText{
        width: 300px;
    }
}

@media screen and (max-width: 900px){
    .footer{
        padding: 2rem;
    }
}

@media screen and (max-width: 800px){
    .topFooter{
        flex-direction: column;
        gap: 2em;
    }
    .topFooter .getLinked .top p.topText{
        width: 100%;
    }
    .topFooter .getLinked .down{
       margin-top: 1.5rem;
    }
}

@media screen and (max-width: 330px){
    .topFooter .getLinked .top a{
        font-size: 20px;
    }
    .topFooter .getLinked .top p.topText{
        font-size: 12px;
    }
    .topFooter .getLinked .down a, 
    .topFooter .usefulLinks ul.mainList li a, .topFooter .contact ul li, .footer .rights{
        font-size: 10px;
    }
    .topFooter .getLinked .down .line{
        height: 20px;
    }
}