.home{
    width: 100%;
    position: relative;
    border-top: 1px solid rgba(255, 255, 255, 0.18);
}

.hidden{
    opacity: 0;
    transform: translateX(-200px);
}

.show{
    opacity: 1;
    transform: translateX(0);
    transition: 2s;
    transition-delay: 500ms;
}

.hideFooter{
    opacity: 0;
    transform: translateY(-100px);
}

.showFooter{
    opacity: 1;
    transform: translateY(0);
    transition: 2s;
    transition-delay: 500ms;
}

@media(prefers-reduced-motion){
    .hidden{
        transition: none;
    }
}

.crossSign{
    position: absolute;
    top: 5%;
    left: 10%;
}

.crossSignUp{
    position: absolute;
    left: 55%;
    top: 5%;
}

.crossSignDown{
    position: absolute;
    top: 30%;
    left: 40%;
}

.backgroundPurple{
    position: absolute;
    right: 0;
    z-index: -1;
    top: 5%;
    bottom: 0;
    width: 50%;
}

.advertDiv{
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 2rem 6rem 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.18);
    overflow: hidden;
}

.headingText{
    position: absolute;
    right: 6rem;
    font-family: 'Montserrat';
}

.headingText h1{
    font-size: 36px;
    font-weight: 600;
    display: flex;
    gap: 0.4rem;
}

.headingText h1 .underlinedText{
    display: flex;
    flex-direction: column;
    align-items: center;
} 

.mainHome{
    width: 100%;
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.textAndTime{
    width: 50%;
}

.mainText .mainHeadOne .firstHead{
    width: 100%;
    position: relative;
}

.mainText .mainHeadOne .firstHead h1{
    width: 100%;
    font-size: 80px;
    font-family: 'Clash Display';
    font-weight: 700;
    display: flex;
    align-items: center;
}

.mainText .mainHeadOne .firstHead h1 .imageAndText{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.mainText .mainHeadOne .firstHead h1 .imageAndText img{
    position: absolute;
    bottom: 70%;
}

.mainText .mainHeadOne .secondHead{
    width: 120%;
    display: flex;
    align-items: center;
    gap: 0;
}

.mainText .mainHeadOne .secondHead h1{
    font-family: 'Clash Display';
    font-size: 80px;
    font-weight: 700;
    display: inline-block;
}

.mainText .mainHeadOne .secondHead h1 span{
    color: #D434FE;
}

.mainText .mainHeadOne .secondHead img{
    z-index: 100;
}

.mainText .participateText{
    width: 80%;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Montserrat';
}

.mainText button{
    margin-top: 1.5rem;
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    background: linear-gradient(270deg, #903AFF 0%, #FE34B9 100%);
}

.mainText button a{
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    font-family: 'Montserrat';
}

.mainText button:hover{
    transform: scale(1.05);
    transition: all .25s ease-in-out;
}

.time{
    margin-top: 3rem;
    display: flex;
    width: 100%;
    align-items: center;
    gap: 1.5rem;
}

.time .timeEach{
    display: flex;
    align-items: flex-end;
}

.time .timeEach .number{
    font-size: 64px;
    font-family: 'Unica One';
    height: 60px;
}

.time .timeEach .timeLetter{
    font-family: 'Montserrat';
    font-size: 14px;
}

.homeImages{
    width: 50%;
    display: flex;
    position: relative;
}

.homeImages img{
    width: 650px;
    margin-top: 3rem;
}

.homeImages .mainImg{
    z-index: -1;
    animation: pulse 2s ease-in-out infinite forwards;
}

.homeImages .behindImg{
    position: absolute;
    right: 0;
    bottom: 0;
    animation: rotateImg 15s ease-in-out infinite;
}

@keyframes rotateImg {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.prizesAndRewards{
    width: 100%;
    padding: 3rem 6rem;
    background:  #100b1f;
    position: relative;
}

.prizesAndRewards .purpStar{
    position: absolute;
    width: 20px;
    left: 20%;
    top: 10%;
    z-index: -1;
}

.prizesAndRewards .whiteStar{
    position: absolute;
    width: 20px;
    left: 50%;
    top: 30%;
    z-index: -1;
}

.prizesAndRewards .whiteStarTwo{
    position: absolute;
    width: 20px;
    right: 10%;
    top: 50%;
    z-index: -1;
}

.prizesAndRewards .greyStar{
    position: absolute;
    width: 15px;
    right: 30%;
    bottom: 5%;
    z-index: -1;
}

.prizesAndRewards .purpLens{
    position: absolute;
    width: 350px;
    transform: rotate(-180deg);
    left: 0;
    z-index: -1;
}

.prizesAndRewards .trophyLens{
    position: absolute;
    right: 0;
    bottom: -30%;
    z-index: -0;
    width: 200px;
}

.prizesHead{
    display: flex;
    flex-direction: column;
    align-items: last baseline;
    width: 100%;
    padding-right: 5rem;
}

.prizesHead h2{
    font-size: 32px;
    font-family: 'Clash Display';
}

.prizesHead .rewards{
    color: #D434FE;
}

.prizesHead p{
    font-family: 'Montserrat';
    margin-top: 1.5rem;
}

.imagesForPrizes{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
}

.imagesForPrizes .trophy{
    width: 500px;
    animation: bounce 5s ease-in-out 1s infinite forwards;
}

.standings{
    width: 50%;
    display: flex;
    font-family: 'Montserrat';
    gap: 1em;
    margin-top: 10rem;
}

.stand{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
}

.stand:nth-child(2){
    margin-top: 2rem;
}

.silver, .bronze{
    width: 150px;
    z-index: 10;
    position: absolute;
    bottom: 70%;
}

.gold{
    width: 250px;
    z-index: 10;
    position: absolute;
    bottom: 70%;
}

.stand .standMoney{
    border-radius: 8px;
    border: 1px solid #D434FE;
    background: rgba(212, 52, 254, 0.12);
    padding: 6rem 1rem 1rem;
}

.stand .standFirst{
    border-radius: 8px;
    border: 1px solid #903AFF;
    background: rgba(144, 58, 255, 0.12);
    padding: 6rem 1rem 1rem;
}

.stand .standFirst .money{
    color: #903AFF;
    margin-top: 0.3em;
    font-size: 30px;
}

.stand .standMoney h2, .stand .standFirst h2{
    font-size: 24px;
    margin-top: 0.25em;
}

.stand .standMoney .money{
    margin-top: 0.3em;
    color: #D434FE;
    font-size: 30px;
}

.partners{
    width: 100%;
    padding: 2rem 6rem;
    position: relative;
    border-bottom: 1px solid rgba(255, 255, 255, 0.18);
}

.partners .partnerPurple{
    position: absolute;
    width: 350px;
    right: 0;
    bottom: 0;
    z-index: -1;
}

.partners .partnerPurpleOne{
    position: absolute;
    width: 450px;
    transform: rotate(-180deg);
    top: 0;
    left: 0;
}

.partners .purpStar{
    position: absolute;
    width: 20px;
    top: 10%;
    left: 10%;
    z-index: -1;
}

.partners .purpStarTwo{
    position: absolute;
    width: 20px;
    top: 40%;
    left: 55%;
    z-index: -1;
}

.partners .greyStar{
    position: absolute;
    width: 20px;
    bottom: 10%;
    left: 50%;
    z-index: -1;
}

.partnersHead{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.partnersHead h1{
    font-family: 'Clash Display';
    font-size: 32px;
}

.partnersHead p{
    font-family: 'Montserrat';
    font-size: 14px;
    width: 450px;
    margin-top: 1rem;
}

.logos{
    width: 100%;
    border-radius: 5px;
    border: 2px solid #D434FE;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5rem 0;
    margin-top: 3rem;
}

.topLogos, .bottomLogos{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3em;
    margin: 1rem auto;
    width: 70%;
    position: relative;
}

.topLogos .eachLogo{
    border-bottom: 2.5px solid #D434FE;
    height: 150px;
    padding-bottom: 1rem;
}

.bottomLogos .eachLogo{
    height: 150px;
}

.eachLogo{
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding-right: 3rem;
}

.eachLogo img{
    cursor: pointer;
    animation: rubberBand 2s ease-in-out 1s infinite forwards;
}

.sideline::before{
    content: '';
    display: block;
    position: absolute;
    top: 30px;
    right: -20px;
    width: 2px;
    height: 90px;
    background-color: #D434FE;
}

.eachLogo .logoText{
    font-family: 'Typo Hoop';
    font-size: 50px;
    cursor: pointer;
    animation: rubberBand 2s ease-in-out 1s infinite forwards;
}

.eachLogo .logoText span{
    color: #00A3FF;
}

.eachLogo .last{
    font-family: 'Voces';
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.eachLogo.last p{
    cursor: pointer;
    animation: rubberBand 2s ease-in-out 1s infinite forwards;
}

.eachLogo .vuzual{
    font-size: 33px;
    cursor: pointer;
    width: 150px; 
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.eachLogo .subscript{
    font-size: 12px;
}

.eachLogo .vuzual span{
    color: #f00;
    font-family: 'Volkhov';
}

.privacy{
    width: 100%;
    padding: 3rem 6rem 6rem;
    display: flex;
    gap: 1em;
    align-items: center;
    justify-content: center;
    position: relative;
}

.privacy .purp{
    position: absolute;
    width: 20px;
    left: 4%;
    bottom: 30%;
}

.privacy .purpTwo{
    position: absolute;
    width: 20px;
    left: 50%;
    top: 30%;
}

.privacy .whiteStar{
    position: absolute;
    width: 15px;
    right: 15%;
    top: 25%;
}

.privacy .whiteStarTwo{
    position: absolute;
    width: 15px;
    left: 50%;
    bottom: 30%;
}

.privacy .purpLens{
    position: absolute;
    width: 350px;
    transform: rotate(-180deg);
    left: 0;
    bottom: 0;
    z-index: -1;
}

.privacy .greyStar{
    position: absolute;
    width: 20px;
    left: 30%;
    top: 7%;
}

.privacy .greyStarTwo{
    position: absolute;
    width: 20px;
    right: 5%;
    bottom: 25%;
}

.privacy .lightPurp{
    position: absolute;
    width: 10px;
    right: 45%;
    bottom: 40%;
}

.privacy .leftSide{
    width: 50%;
}

.privacy .leftSide .headText{
    width: 100%;
}

.privacy .leftSide .headText h1{
    font-family: 'Clash Display';
    width: 100%;
}

.privacy .leftSide .headText h1 span{
    color: #D434FE;
}

.privacy .leftSide .headText p.greyText{
    margin-top: 0.75rem;
    font-family: 'Montserrat';
    color: rgba(255, 255, 255, 0.75);
    font-size: 14px;
}

.privacy .leftSide .headText p{
    margin-top: 1rem;
    font-family: 'Montserrat';
    font-size: 16px;
}

.privacy .leftSide .bodyText{
    margin-top: 2rem;
    border-radius: 5px;
    border: 1px solid #D434FE;
    background: rgba(217, 217, 217, 0.03);
    padding: 3rem;
    width: 90%;
}

.privacy .leftSide .bodyText p{
    font-family: 14px;
    font-family: 'Montserrat';
    width: 100%;
}

.privacy .leftSide .bodyText .licensing{
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.privacy .leftSide .bodyText .licensing h2{
    font-weight: 600;
    color: #D434FE;
    font-size: 16px;
    font-family: 'Montserrat';
}

.privacy .leftSide .bodyText .licensing p.bold{
    margin-top: 1rem;
    font-weight: 600;
}

.privacy .leftSide .bodyText .licensing ul{
    list-style: none;
    width: 100%;
    margin-top: 0.75rem;
}

.privacy .leftSide .bodyText .licensing ul li{
    width: 100%;
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    text-align: left;
    gap: 0.5rem;
    font-size: 14px;
}

.privacy .leftSide .bodyText .licensing .registerBtn{
    width: 150px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    background: linear-gradient(270deg, #903AFF 0%, #FE34B9 100%);
    margin-top: 1.5rem;
}

.privacy .leftSide .bodyText .licensing .registerBtn a{
    text-decoration: none;
    color: #fff;
    font-size: 14px;
    font-family: 'Montserrat';
}

.privacy .leftSide .bodyText .licensing .registerBtn:hover{
    transform: scale(1.05);
    transition: all .25s ease-in-out;
}

.privacy .rightSide{
    width: 50%;
    position: relative;
    display: flex;
    flex-direction: column;
    animation: bounce 5s ease-in-out 1s infinite forwards;
}

.privacy .rightSide img.privatePic{
    width: 400px;
    z-index: 10;
}

.privacy .rightSide img.bgSecurity{
    position: absolute;
    width: 300px;
    top: -5%;
    left: 15%;
}

