.mainDiv{
    width: 100%;
}

.completeBg{
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    background: rgba(21, 14, 40, 0.93);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    display: grid;
    place-items: center;
    animation: backInDown 2s ease-in-out forwards;
}

.completeBg .complete{
    border-radius: 5px;
    border: 1px solid #D434FE;
    background: rgba(255, 255, 255, 0.01);
    padding: 2rem;
    display: grid;
    place-items: center;
    font-family: 'Montserrat';
}

.completeBg .complete .images{
    width: 100%;
    display: flex;
    position: relative;
    justify-content: center;
}

.completeBg .complete .images img{
    width: 200px;
}

.completeBg .complete .images img.tick{
    position: absolute;
    left: 0;
}

.completeBg .complete .images img.successMan{
    margin-left: 2rem;
    z-index: 10;
}

.completeBg .complete h2{
    text-align: center;
    font-size: 20px;
    margin-top: 1rem;
    line-height: 1.5;
}

.completeBg .complete .smallTexts{
    margin-top: 1rem;
    text-align: center;
    font-size: 14px;
    line-height: 1.5;
}

.completeBg .complete .smallTexts .wink{
    display: flex;
    align-items: center;
    gap: 0.5em;
}

.completeBg .complete button{
    width: 100%;
    margin-top: 1rem;
    border: none;
    height: 40px;
    font-family: 'Montserrat';
    font-weight: 600;
    color: #FFF;
    border-radius: 4px;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
    cursor: pointer;
}

.completeBg .complete button:hover{
    transform: scale(1.05);
    transition: all .3s ease-in-out;
}

.register{
    width: 100%;
    padding: 2rem 6rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.register img.lens{
    position: absolute;
    transform: rotate(180deg);
    width: 500px;
    bottom: 0;
    right: 0;
    z-index: -10;
}

.register img.whitestar{
    position: absolute;
    width: 10px;
    right: 10%;
    bottom: 5%;
}

.register img.greyStar{
    position: absolute;
    width: 20px;
    right: 15%;
    top: 10%;
}

.register img.greyStarTwo{
    position: absolute;
    width: 20px;
    left: 10%;
    bottom: 10%;
}

.register img.purpleStar{
    position: absolute;
    width: 20px;
    left: 45%;
    bottom: 30%;
}

.register img.otherStar{
    position: absolute;
    width: 20px;
    left: 5%;
    top: 10%;
}

.register img.manSitting{
    width: 500px;
    animation: wobble 5s ease-in-out 1s infinite;
}

.registerSide{
    width: 50%;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 3rem;
    opacity: 0;
    animation: fadeInDown 2s ease-in-out 1.5s forwards;
}

.registerSide h2{
    font-family: 'Clash Display';
    color: #D434FE;
    font-size: 32px;
}

.registerSide .movement{
    margin-top: 1.5rem;
    font-family: 'Montserrat';
    display: flex;
    align-items: flex-end;
    gap: 1rem;
}

.registerSide .movement p{
    font-size: 14px;
}

.registerSide .movement .images{
    border-bottom: 1px dashed #D434FE;
}

.registerSide p.largeText{
    font-size: 24px;
    font-family: 'Montserrat';
    margin-top: 0.75rem;
}

.registerSide form{
    margin-top: 1.5rem;
    width: 100%;
}

.registerSide form .row{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    gap: 1rem;
}

.registerSide form .row .eachQuest{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    font-family: 'Montserrat';
    width: 50%;
}

.registerSide form .row .eachQuest input, .registerSide form .row .eachQuest select{
    width: 100%;
    height: 40px;
    border-radius: 4px;
    border: 1px solid #FFF;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 0 1rem;
    color: #fff;
    font-family: 'Montserrat';
}

.registerSide form .row .eachQuest input::placeholder{
    font-family: 'Montserrat';
    font-size: 12px;
}

.registerSide form p.small{
    font-size: 12px;
    font-family: 'Montserrat';
    margin-top: 1.5rem;
    color: #FF26B9;
}

.registerSide form .checkbox{
    display: flex;
    align-items: center;
    margin-top: 1rem;
    font-family: 'Montserrat';
    gap: 0.35rem;
    font-size: 14px;
}

.registerSide form button{
    width: 100%;
    margin-top: 1rem;
    border-radius: 4px;
    border: none;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
    font-family: 'Montserrat';
    cursor: pointer;
    color: #fff;
    height: 40px;
    font-weight: 600;
}

.registerSide form button:hover{
    transform: scale(1.05);
    transition: all .35s ease-in-out;
}

@media screen and (max-width: 1250px){
    .registerSide form .row .eachQuest input, .registerSide form .row .eachQuest select{
        padding: 0 0.25rem;
    }
}

@media screen and (max-width: 1150px){
    .register img.manSitting{
        width: 300px;
    }
    .registerSide{
        width: 60%;
        padding: 1.5rem;
    }
    .registerSide p.largeText{
        font-size: 20px;
    }
}

@media screen and (max-width: 900px){
    .register{
        padding: 2rem;
    }
}

@media screen and (max-width: 800px){
    .completeBg{
        min-height: 100vh;
    }
    .register{
        flex-direction: column;
    }
    .registerSide{
        width: 100%;
        border-radius: unset;
        background: transparent;
        box-shadow: none;
    }
    .register img.lens{
        width: 300px;
    }
    .register img.purpleStar{
        left: unset;
        right: 3%;
        bottom: 20%;
    }
    .register img.greyStarTwo{
        left: 2%;
    }
    .registerSide form .row .eachQuest input, .registerSide form .row .eachQuest select{
        padding: 0 1rem;
    }
    .registerSide form .row{
        align-items: flex-start;
        flex-direction: column;
    }
    .registerSide form .row .eachQuest{
        width: 100%;
    }
}

@media screen and (max-width: 500px){
    .registerSide{
        padding: 0;
    }
    .register img.manSitting{
        width: 200px;
    }
    .register{
        padding: 0.5rem;
    }
}

@media screen and (max-width: 420px){
    .completeBg .complete .images img{
        width: 100px;
    }
    .completeBg .complete .images img.tick{
        left: 30%;
    }
    .completeBg .complete h2{
        font-size: 15px;
    }
    .completeBg .complete .smallTexts{
        font-size: 10px;
    }
}

@media screen and (max-width: 340px){
    .registerSide h2{
        font-size: 20px;
    }
    .registerSide .movement p{
        font-size: 12px;
    }
    .registerSide p.largeText{
        font-size: 18px;
    }
    .registerSide form .row .eachQuest label{
        font-size: 12px;
    }
}