.frequent{
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.18);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4rem 6rem;
}

.questions{
    width: 50%;
    position: relative;
}

.questions .purp{
    position: absolute;
    left: -5%;
    width: 15px;
}

.questionHead{
    width: 100%;
}

.questionHead h2{
    font-family: 'Clash Display';
    font-size: 30px;
}

.questionHead .purple{
    color: #D434FE;
}

.questionHead p{
    margin-top: 1.5rem;
    font-size: 16px;
    font-weight: 400;
    font-family: 'Montserrat';
    width: 70%;
}

.divOfQuestions{
    width: 100%;
    margin-top: 2rem;
    font-family: 'Montserrat';
}

.divOfQuestions .eachQuestion{
    width: 100%;
    display: flex;
    border-bottom: 1px solid #D434FE;
    justify-content: space-between;
    align-items: center;
    gap: 0.5em;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    cursor: pointer;
}

.divOfQuestions .eachQuestion .plus{
    font-size: 20px;
    color: #D434FE;
    font-weight: 700;
    cursor: pointer;
}

.images .cloudImg{
    width: 500px;
    animation: bounce 5s ease-in-out 1s infinite forwards;
}

.images{
    position: relative;
}

.images .front{
    position: absolute;
    top: -15%;
    left: 30%;
    font-size: 100px;
    font-family: 'Made Tommy Outline';
    color: #D434FE;
}

.images .behindLeft{
    position: absolute;
    font-size: 80px;
    font-family: 'Made Tommy Outline';
    color: #A866FD;
    top: -5%;
    left: 10%;
}

.images .behindRight{
    position: absolute;
    font-size: 80px;
    font-family: 'Made Tommy Outline';
    color: #A866FD;
    top: -5%;
    left: 50%;
}

.images .starFirst{
    position: absolute;
    top: -5%;
    left: 45%;
    width: 15px;
}

.images .starSecond{
    position: absolute;
    top: 20%;
    left: 18%;
    width: 15px;
}

.images .starWhite{
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 15px;
}

.images .starGrey{
    position: absolute;
    width: 15px;
    top: 50%;
    left: 0;
}

@media screen and (max-width: 1200px){
    .images .cloudImg{
        width: 350px;
    }
    .questions{
        width: 55%;
    } 
    .images .behindLeft, .images .front,  .images .behindRight{
        top: -30%;
    }
}

@media screen and (max-width: 1030px){
    .images .cloudImg{
        width: 300px;
    }
}

@media screen and (max-width: 900px){
    .frequent{
        padding: 4rem 2rem;
    }
    .questionHead p{
        font-size: 13px;
        width: 100%;
    }
    .divOfQuestions .eachQuestion p{
        font-size: 13px;
    }
    .images .cloudImg{
        width: 250px;
    }
    .questions{
        width: 50%;
    } 
    .images .behindLeft, .images .front,  .images .behindRight{
        top: -40%;
    }
}

@media screen and (max-width: 800px){
    .frequent{
       flex-direction: column;
       gap: 5rem;
    }
    .questions{
        width: 100%;
    } 
    .images .behindLeft{
        left: -10%;
    }
    .images .behindRight{
        left: 70%;
    }
    .questionHead h2{
        text-align: center;
        font-size: 22px;
    }
}

@media screen and (max-width: 380px){
    .images .cloudImg{
        width: 150px;
    }
    .images .behindLeft, .images .front,  .images .behindRight{
        top: -50%;
        font-size: 50px;
    }
    .frequent{
        padding: 1rem;
    }

}