.overview{
    width: 100%;
}

.firstOverview, .secondOverview, .thirdOverview{
    width: 100%;
    position: relative;
    padding: 2rem 6rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.18);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.firstOverview .arrow{
    position: absolute;
    left: 40%;
    bottom: 10%;
    animation: bounce 5s ease-in-out 1s infinite forwards;
}

.firstOverview .purpleStar{
    position: absolute;
    top: 32%;
    right: 8%;
    z-index: -10;
}

.firstOverview .starShine{
    position: absolute;
    z-index: -10;
}

.firstOverview .bigIdeaText, .secondOverview .bigIdeaText{
    width: 50%;
}

.firstOverview .bulb{
    width: 500px;
    animation: swing 5s ease-in-out 1s infinite forwards;
}

.textHeader{
    display: flex;
    flex-direction: column;
    font-family: 'Clash Display';
    font-size: 30px;
    line-height: 1.2;
}

.textHeader .purpleHead{
    color: #D434FE;
}

.bigIdeaText p{
    margin-top: 1rem;
    font-family: 'Montserrat';
    font-size: 15px;
    width: 85%;
}

.secondOverview .sitting{
    width: 500px;
    animation: tada 5s ease-in-out 1s infinite forwards;
}

.secondOverview .whiteStar{
    position: absolute;
    left: 50%;
    bottom: 10%;
    z-index: -10;
}

.secondOverview .greyStar{
    position: absolute;
    left: 25%;
    top: 15%;
    z-index: -10;
}

.secondOverview .purpleLens{
    position: absolute;
    width: 45%;
    bottom: 0;
    z-index: -10;
}

.secondOverview .purpleLensTwo{
    position: absolute;
    right: 0;
    top: 0;
    z-index: -10;
}

.thirdOverview .judge{
    width: 500px;
    animation: wobble 5s ease-in-out 1s infinite forwards;
}

.thirdOverview{
    padding: 5rem 6rem;
}

.thirdOverview .purpleLensThird{
    position: absolute;
    width: 45%;
    bottom: 0;
    left: 0;
    z-index: -10;
}

.thirdOverview .purpleLensThirdBelow{
    position: absolute;
    width: 30%;
    top: 60%;
    left: 70%;
    z-index: -10;
    transform: rotate(-180deg);
}

.thirdOverview .purpleStar{
    position: absolute;
    top: 10%;
    width: 30px;
    left: 15%;
    z-index: -10;
}

.thirdOverview .whiteStar{
    position: absolute;
    left: 45%;
    bottom: 10%;
    width: 30px;
    z-index: -10;
}

.thirdOverview .greyStar{
    position: absolute;
    width: 30px;
    left: 25%;
    bottom: 35%;
    z-index: -10;
}

.attributes{
    width: 50%;
}

.attributes .attributeHead{
    margin-bottom: 1rem;
    width: 100%;
    font-family: 'Clash Display';
    font-size: 32px;
    line-height: normal;
}

.attributes .attributeHead .second{
    color: #D434FE;
}

.attributes .keys{
    margin-top: 1rem;
    width: 100%;
    font-family: 'Montserrat';
    font-size: 16px;
    font-weight: 400;
}

.attributes .keys p{
    line-height: 1.5;
    margin-top: 1rem;
}

.attributes .keys p .keyName{
    font-weight: 700;
    color: #D434FE;
}

.attributes button{
    margin-top: 2rem;
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    background: linear-gradient(270deg, #903AFF 0%, #FE34B9 100%);
}

.attributes button a{
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    font-family: 'Montserrat';
}

.attributes button:hover{
    transform: scale(1.05);
    transition: all .25s ease-in-out;
}

@media screen and (max-width: 1350px) {
    h2{
        font-size: 30px;
    }
}

@media screen and (max-width: 1260px) {
    .firstOverview .bulb, .secondOverview .sitting{
        width: 400px;
    }
    .bigIdeaText p{
        width: 100%;
    }
}

@media screen and (max-width: 1030px){
    .firstOverview .bulb, .secondOverview .sitting{
        width: 300px;
    }
    .thirdOverview .judge{
        width: 400px;
    }
    .thirdOverview{
        padding: 3rem 6rem;
    }
    h2{
        font-size: 27px;
    }
}

@media screen and (max-width: 900px) {
    .firstOverview, .secondOverview, .thirdOverview{
        padding: 2rem;
    }
    .bigIdeaText p{
        font-size: 13px;
    }
    .attributes .keys p{
        width: 100%;
        font-size: 13px;
    }
}

@media screen and (max-width: 800px) {
    .firstOverview, .thirdOverview{
       flex-direction: column;
       gap: 2rem;
    }
    .secondOverview{
        flex-direction: column-reverse;
       gap: 2rem;
    }
    .firstOverview .bulb, .secondOverview .sitting, .thirdOverview .judge{
        width: 250px;
    }
    .firstOverview .bigIdeaText, .secondOverview .bigIdeaText{
        width: 100%;
        text-align: center;
    }
    .firstOverview .arrow{
        left: 50%;
        bottom: 40%;
        width: 30px;
    }
    .attributes{
        width: 100%;
        text-align: center;
    } 
    .secondOverview .whiteStar{
        left: 5%;
        bottom: 30%;
        width: 20px;
    }  
    .thirdOverview .whiteStar{
        left: 5%;
        bottom: 30%;
        width: 20px;
    } 
}

@media screen and (max-width: 460px) {
    .firstOverview .arrow{
        bottom: 50%;
        width: 20px;
    }
    .firstOverview .bulb{
        width: 180px;
    }
}

@media screen and (max-width: 410px) {
    h2{
        font-size: 20px;
    }
    .secondOverview .purpleLens{
        width: 60%;
        bottom: unset;
        top: 30%;
        left: 0;
    }
    .secondOverview .purpleLensTwo{
        width: 30%;
        bottom: 0;
        top: unset;
    }
}


@media screen and (max-width: 335px) {
    h2{
        font-size: 16px;
    }
    .bigIdeaText p{
        font-size: 10px;
    }
    .attributes .keys p{
        font-size: 10px;
    }
    .firstOverview .arrow{
        bottom: 45%;
    }
}

@media screen and (max-width: 280px) {
    h2{
        font-size: 12px;
    }
    .firstOverview .bulb, .secondOverview .sitting, .thirdOverview .judge{
        width: 160px;
    }
}