.header{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 1.5rem 6rem 0.75rem;
    justify-content: space-between;
    position: sticky;
    top: 0;
    background-color: #150E28;
    z-index: 10;
}

.header .logoText{
    text-decoration: none;
    color: #fff;
    font-size: 36px;
    font-family: 'Clash Display';
    font-weight: 700;
    opacity: 0;
    animation: fadeInDown .5s ease-in 1s forwards;
}

.header .logoText p:hover{
    transform: scale(1.2);
    transition: all .3s ease-in-out;
}

.header .logoText p span{
    color: #D434FE;
}

.header .navAndBtn{
    display: flex;
    align-items: center;
    gap: 3.5rem;
    opacity: 0;
    animation: fadeInDown .5s ease-in 1.5s forwards;
}

.header .navAndBtn nav ul{
    display: flex;
    list-style-type: none;
    gap: 2rem;
    align-items: center;
    font-family: 'Montserrat';
    font-size: 16px;
}

.header .navAndBtn nav ul li a{
    text-decoration: none;
    color: #fff;
}

.header .navAndBtn nav ul li a:hover{
    color: rgba(255, 255, 255, 0.18);
    transition: color .25s ease-in-out;
}

.header .navAndBtn nav ul li .contactLink{
    background: linear-gradient(90deg, #903AFF 3.08%, #FF26B9 93.85%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 600;
}

.header .navAndBtn button{
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 4px;
    cursor: pointer;
    background: linear-gradient(270deg, #903AFF 0%, #FE34B9 100%);
}

.header .navAndBtn button a{
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    font-family: 'Montserrat';
}

.header .menuBtn{
    display: none;
    cursor: pointer;
    z-index: 9999;
    opacity: 0;
    animation: fadeInDown .5s ease-in 1.5s forwards;
}

.header .navAndBtn button:hover{
    transform: scale(1.05);
    transition: all .25s ease-in-out;
}

@media screen and (max-width: 900px) {
    .header{
        padding: 1.5rem 2rem 0.75rem;
    }
}

@media screen and (max-width: 770px) {
    .header .navAndBtn{
        display: none;
        flex-direction: column;
        align-items: flex-start;
        gap: 1.5rem;
        position: absolute;
        background-color: #150E28;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        padding: 5rem 1rem;
        min-height: 100vh;
    }

    .header .navAndBtn.active{
        display: flex;
        animation: fadeInLeft .35s ease-in forwards;
    }

    .header .navAndBtn nav ul{
       flex-direction: column;
       align-items: flex-start;
       gap: 1.5rem;
    }
    .header .menuBtn{
        display: block;
    }
    
}

@media screen and (max-width: 450px) {
    .header{
        padding: 1rem 1.25rem 0.75rem;
    }
    .header .logoText{
        font-size: 20px;
    }
}
