.timeline{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem 2rem;
}

.timelineHead{
    text-align: center;
}

.timelineHead h2{
    font-family: 'Clash Display';
    font-size: 32px;
}

.timelineHead p{
    font-family: 'Montserrat';
    font-size: 14px;
    margin-top: 1rem;
}

.timelineDiv{
    margin-top: 2rem;
    position: relative;
}

.timelineDiv .purp{
    position: absolute;
    width: 20px;
    left: 10%;
}

.timelineDiv .grey{
    position: absolute;
    width: 20px;
    bottom: 0;
}

.eachTimeline{
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 5em;
    align-items: center;
    font-family: 'Montserrat';
    margin-top: 2.5em;
}

.eachTimeline:nth-child(even){
    flex-direction: row-reverse;
}

.eachTimeline .firstSide{
    width: 33%;
}

.eachTimeline .firstSide h3{
    color: #D434FE;
    font-weight: 600;
}

.eachTimeline .firstSide p{
    font-size: 14px;
    width: 100%;
    margin-top: 0.75em;
}

.eachTimeline .numbers{
    position: relative;
}

.eachTimeline .numbers .number{
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 100%);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
}

.eachTimeline .numbers{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.35em;
}

.eachTimeline .numbers .number::before{
    content: '';
    width: 4px;
    height: 50px;
    background-color: #D434FE;
    position: absolute;
    top: -60px;
}

.eachTimeline .secondSide{
    width: 33%;
    color: #D434FE;
    font-weight: 600;
}

@media screen and (max-width: 900px){
    .timeline{
        padding: 2rem;
    }
}

@media screen and (max-width: 700px){
    .timeline{
        padding: 2rem;
    }
    .eachTimeline .firstSide h3{
        font-size: 20px;
    }
    .eachTimeline .firstSide p{
        font-size: 12px;
    }
}

@media screen and (max-width: 580px){
    .timelineDiv{
        margin-top: 1rem;
    }
    .eachTimeline{
        flex-direction: column;
        position: relative;
        gap: 0.5em;
        margin-top: 3em;
    }
    .eachTimeline:nth-child(even){
        flex-direction: column;
        position: relative;
    }
    .eachTimeline .firstSide, .eachTimeline .secondSide{
        width: 75%;
        margin-left: 2em;
    }
    .eachTimeline .numbers{
        position: absolute;
        left: 0;
        top: 60px;
        height: 100%;
    }
    .eachTimeline .firstSide h3{
        font-size: 15px;
    }
    .eachTimeline .secondSide h3{
        font-size: 15px;
    }
    .eachTimeline .numbers .number{
        width: 30px;
        height: 30px;
    }
}